.render-projects {
  display: grid;
  grid-template-columns: 1fr 1fr;
  margin-top: 0px !important;
  grid-column-gap: 5.859vw;
  min-height: 100vh;
}
@media (max-width: 1150px) {
  .render-projects {
    grid-column-gap: 6.522vw;
  }
}
@media (max-width: 900px) {
  .render-projects {
    grid-column-gap: 5.556vw;
  }
}
@media (max-width: 700px) {
  .render-projects {
    grid-column-gap: 40px;
  }
}
@media (max-width: 600px) {
  .render-projects {
    grid-template-columns: 1fr;
    grid-gap: 40px;
  }
}
@media (max-width: 450px) {
  .render-projects {
    grid-gap: 30px;
  }
}
.render-projects .load-init {
  transition-delay: 666ms;
}
.render-projects .item {
  overflow: hidden;
  -webkit-clip-path: inset(0 0 0 0);
          clip-path: inset(0 0 0 0);
  height: 44.922vw;
}
@media (max-width: 1150px) {
  .render-projects .item {
    height: 50vw;
  }
}
@media (max-width: 900px) {
  .render-projects .item {
    height: 47.778vw;
  }
}
@media (max-width: 700px) {
  .render-projects .item {
    height: 358px;
  }
}
@media (max-width: 450px) {
  .render-projects .item {
    height: 405px;
  }
}
.render-projects .item .image-animate {
  height: 100%;
  transition-delay: 620ms !important;
}
.render-projects .item .custom-show {
  opacity: 0;
  transform: translateY(200px);
  -webkit-animation: animationFade 1000ms cubic-bezier(0.19, 1, 0.22, 1) 233ms forwards;
          animation: animationFade 1000ms cubic-bezier(0.19, 1, 0.22, 1) 233ms forwards;
}
@-webkit-keyframes animationFade {
  from {
    opacity: 0;
    transform: translateY(200px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
@keyframes animationFade {
  from {
    opacity: 0;
    transform: translateY(200px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
.render-projects .item .wrapper {
  height: 100%;
  position: relative;
  transition: transform 0.1s ease, opacity 0.25s ease;
}
.render-projects .item .wrapper img {
  width: 100%;
  height: 150%;
  -o-object-fit: cover;
     object-fit: cover;
}
@media (max-width: 700px) {
  .render-projects .item .wrapper img {
    height: 100%;
  }
}
.render-projects .item .wrapper .info {
  position: absolute;
  top: 3.516vw;
  left: 3.516vw;
  right: 3.516vw;
  bottom: 3.516vw;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 1150px) {
  .render-projects .item .wrapper .info {
    top: 3.913vw;
    left: 3.913vw;
    right: 3.913vw;
    bottom: 3.913vw;
  }
}
@media (max-width: 900px) {
  .render-projects .item .wrapper .info {
    top: 5vw;
    left: 5vw;
    right: 5vw;
    bottom: 5vw;
  }
}
@media (max-width: 700px) {
  .render-projects .item .wrapper .info {
    top: 32px;
    left: 32px;
    right: 32px;
    bottom: 32px;
  }
}
.render-projects .item .wrapper .info .category {
  font-size: 1.25vw;
}
@media (max-width: 1150px) {
  .render-projects .item .wrapper .info .category {
    font-size: 1.391vw;
  }
}
@media (max-width: 900px) {
  .render-projects .item .wrapper .info .category {
    font-size: 1.778vw;
  }
}
@media (max-width: 700px) {
  .render-projects .item .wrapper .info .category {
    font-size: 14px;
  }
}
.render-projects .item .wrapper .info .title {
  font-size: 2.5vw;
  line-height: 132.9%;
}
@media (max-width: 1150px) {
  .render-projects .item .wrapper .info .title {
    font-size: 3.556vw;
  }
}
@media (max-width: 900px) {
  .render-projects .item .wrapper .info .title {
    font-size: 2.667vw;
  }
}
@media (max-width: 700px) {
  .render-projects .item .wrapper .info .title {
    font-size: 20px;
  }
}
.render-projects .item:nth-child(even) {
  margin-top: 5.859vw;
}
@media (max-width: 1150px) {
  .render-projects .item:nth-child(even) {
    margin-top: 6.522vw;
  }
}
@media (max-width: 900px) {
  .render-projects .item:nth-child(even) {
    margin-top: 5.556vw;
  }
}
@media (max-width: 700px) {
  .render-projects .item:nth-child(even) {
    margin-top: 40px;
  }
}
@media (max-width: 600px) {
  .render-projects .item:nth-child(even) {
    margin-top: 0px;
  }
}
@media (max-width: 900px) {
  .render-projects .movable-button {
    display: none;
  }
}