.image-section {
  height: 100vh;
  overflow: hidden;
  position: relative;
  transition-delay: 666ms !important;
}
@media (max-width: 700px) {
  .image-section {
    height: 374px;
  }
}
.image-section img {
  transition: transform 0.1s ease, opacity 0.25s ease;
  width: 100%;
  height: 150%;
  -o-object-fit: cover;
     object-fit: cover;
}
@media (max-width: 700px) {
  .image-section img {
    height: 100%;
  }
}