.project-template .img-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: -1;
}
.project-template .img-wrapper img {
  height: 150%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
@media (max-width: 700px) {
  .project-template .img-wrapper img {
    height: 100%;
  }
}
.project-template .project-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
@media (max-width: 700px) {
  .project-template .project-info {
    grid-template-columns: 1fr;
    grid-gap: 20px;
  }
}
