@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;500;600;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-backface-visibility: hidden;
}

html,
body {
  scroll-behavior: smooth;
}

body {
  background-color: #000;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  font-size: 1.406vw;
  line-height: 145.9%;
}
@media (max-width: 1150px) {
  body {
    font-size: 1.304vw;
  }
}
@media (max-width: 900px) {
  body {
    font-size: 16px;
  }
}
body ::-moz-selection {
  color: #000;
  background: #fff;
}
body ::selection {
  color: #000;
  background: #fff;
}

.container {
  margin-left: 4.688vw;
  margin-right: 4.688vw;
}
@media (max-width: 1150px) {
  .container {
    margin-left: 5.217vw;
    margin-right: 5.217vw;
  }
}
@media (max-width: 900px) {
  .container {
    margin-left: 4.444vw;
    margin-right: 4.444vw;
  }
}
@media (max-width: 700px) {
  .container {
    margin-left: 40px;
    margin-right: 40px;
  }
}
@media (max-width: 450px) {
  .container {
    margin-left: 30px;
    margin-right: 30px;
  }
}

.container-narrow {
  width: 76.563vw;
  margin: auto;
}
@media (max-width: 1150px) {
  .container-narrow {
    width: 80.87vw;
  }
}
@media (max-width: 900px) {
  .container-narrow {
    width: 82.222vw;
  }
}
@media (max-width: 700px) {
  .container-narrow {
    width: initial;
    margin-left: 40px;
    margin-right: 40px;
  }
}
@media (max-width: 450px) {
  .container-narrow {
    margin-left: 30px;
    margin-right: 30px;
  }
}

.hover {
  overflow: hidden;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  position: relative;
  cursor: pointer;
}
.hover .fake {
  position: absolute;
  transform: translateY(10%);
}
.hover p {
  transform: translateY(0%);
}
.hover p,
.hover .fake {
  transition: 400ms cubic-bezier(0.165, 0.84, 0.44, 1);
}
.hover:hover p {
  transform: translateY(-105%);
}
.hover:hover .fake {
  transform: translateY(-100%);
}

.section-space {
  margin-top: 15.625vw;
  margin-bottom: 15.625vw;
}
@media (max-width: 1150px) {
  .section-space {
    margin-top: 17.391vw;
    margin-bottom: 17.391vw;
  }
}
@media (max-width: 900px) {
  .section-space {
    margin-top: 16.667vw;
    margin-bottom: 16.667vw;
  }
}
@media (max-width: 700px) {
  .section-space {
    margin-top: 120px;
    margin-bottom: 120px;
  }
}

.white-section {
  background-color: #fff;
  color: #000;
}
.white-section .button-dark {
  background-color: #000;
  border: 1px solid #000;
}
.white-section .button-dark:hover {
  background-color: transparent;
}

svg {
  aspect-ratio: 1/1;
}

video {
  width: 100%;
}

h1 {
  font-size: 5.938vw;
  line-height: 104%;
  font-weight: 300;
  letter-spacing: -0.015rem;
}
@media (max-width: 1150px) {
